.modal_order {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	z-index: 49;

	// display: flex;
	// // display: none;
	// align-items: center;
	// justify-content: center;

	background-color: rgba(24, 23, 43, .7);

	visibility: hidden;

	overflow-y: auto;

	@include min1050 {
		background-color: #211F3D;
	}

	&__close_smart {
		display: none;

		&:hover {
			svg {
				line {
					stroke: var(--c-yellow);
				}
			}
		}

		width: rem(40);
		height: rem(40);
		z-index: 2;

		svg {
			width: rem(40) !important;
			height: rem(40) !important;

			@include min540 {
				width: rem(28) !important;
				height: rem(28) !important;
			}
			line {
				stroke: #B2B1BE;
			}
		}

		@include min1050 {
			position: absolute;
			top: rem(68);
			right: rem(50);
			// display: block;
		}

		@include min540 {
			top: rem(24);
			right: rem(15);

			width: rem(28) !important;
			height: rem(28) !important;
		}
	}

	&__close {
		position: absolute;
		top: rem(20);
		right: rem(20);

		@include min1050 {
			display: none;
		}

			&:hover {
				svg {
					line {
						stroke: var(--c-yellow);
					}
				}
			}

			svg {
				line {
					stroke: #B2B1BE;
				}
			}
		}

	&__header {
		position: relative;
		padding-bottom: rem(26);
		margin-bottom: rem(28);

		&::before {
			content: "";
			position: absolute;
			bottom: 0;
			left: -100%;
			width: 9999em;
			height: rem(1.5);
			background-color: var(--c-text-dark);
		}

		.title {
			margin-bottom: 0;
		}
	}

	&__wrap {
		position: absolute;
		top: 0;
		left: 50%;
		margin-left: rem(-207.5);
		width: rem(415);
		background-color: #211F3D;
		border-radius: rem(5);
		
		display: flex;
		flex-direction: column;

		padding: rem(44) rem(30);

		transform: translateY(-100%);
		transition: var(--transition);

		overflow: hidden;

		@include min460 {
			width: 100%;
			min-width: 320px;
			left: 0;
			margin-left: 0;
		}


		.field_set_radio {
			margin-bottom: rem(22);
		}

		.text_result {
			margin: rem(60) 0;

			.top {
				margin-bottom: rem(20);
				color: var(--c-text-gray);
				display: flex;
				align-items: center;

				@include min540 {
					margin-bottom: rem(10);

					span {
						font-size: rem(15);
					}
				}

				.mobile {
						display: none;

						@include min540 {
							display: flex;
						}
					}

					.desktop {
						@include min540 {
							display: none;
						}
					}

				.count {
					color: var(--c-pink);
					margin-left: rem(2);
					
				}
			}

			.bottom {
				font-size: rem(15);
				color: #828282;

				@include min540 {
					font-size: rem(13);
				}
			}
		}

		.button_yellow {
			width: 100%;
			flex: 0 0 100%;
			max-width: 100%;
		}
		

		.title {
			font-size: rem(25);
			color: var(--c-text-gray-light);
			font-weight: 500;
			margin-bottom: rem(20);
			margin-left: auto;
			margin-right: auto;

			@include min540 {
				font-size: rem(20);
				margin-bottom: rem(8);
			}
		}

		.title.mb-5 {
			margin-bottom: rem(5);
		}

		// .flex {
		// 	flex-direction: column;
		// 	align-items: center;

		// 	@include min540 {
		// 		flex-direction: row;
				
		// 		.text {
		// 			margin-right: rem(6);
		// 		}

		// 		.username {
		// 			margin-bottom: 0;
		// 			border-bottom: 0;
		// 			text-decoration: underline;
		// 		}
		// 	}
		// }

		.desktop {
			@include min540 {
				display: none;
			}
		}

		.mobile {
			display: none;

			.text {
				text-align: left;
				span {
					color: var(--c-yellow);
					text-decoration: underline;
					word-break: break-all;
				}
			}

			@include min540 {
				display: block;
			}
		}

		.text {
			color: white;
			text-align: center;

			@include min540 {
				font-size: rem(15);
			}
		}

		.username {
			color: var(--c-yellow);
			margin-top: rem(20);
			font-size: rem(15);
			border-bottom: rem(1.5) solid var(--c-yellow);

			margin-left: auto;
			margin-right: auto;
			align-self: baseline;
		}
		
	}

	.title_border {
		text-align: center;
		font-size: rem(25);
		font-weight: 500;
		color: var(--c-text-gray-light);

		@include min540 {
			text-align: left;
			font-size: rem(20);
			font-weight: 800;
			padding-bottom: rem(34);
			
			position: relative;

			&::before {
				content: "";
				position: absolute;
				bottom: 0;
				left: -100%;
				width: 9999em;
				height: rem(1.5);
				background-color: var(--c-text-dark);
			}
		}
	}

	&__avatar {
		margin-top: rem(42);
		margin-bottom: rem(42);
		cursor: pointer;
		background-color: #2F2E4A;
		border: rem(1.5) solid #18172A;
		border-radius: rem(4);
		height: rem(208);
		width: 100%;
		transition: var(--transition);
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		@include min1050 {
			height: rem(54);
			margin-bottom: rem(64);
			border: 0;
			background-color: var(--c-yellow);
		}

		@include min540 {
			margin-bottom: rem(24);
		}

		input {
			display: none;
		}

		.text-mobile {
			display: none;

			@include min1050 {
				display: block;
				font-weight: 500;
				color: var(--c-text-dark);
			}
		}

		.icon {
			margin-bottom: rem(18);

			@include min1050 {
				display: none;
			}
		}

		.text {
			font-weight: 500;
			font-size: rem(15);
			color: var(--c-text-gray-light);

			@include min1050 {
				display: none;
			}
		}
	}

	&__avatar.highlight {

		@include max991 {
			border-color: var(--c-yellow);
		}
		
	}

	&__setting_form {
		margin-top: rem(42);
		
		display: flex;
		flex-direction: column;

		.field {
			margin-bottom: rem(30);

			@include min540 {
				margin-bottom: rem(15);
			}
		}

		.form_bottom {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;

			@include min540 {
				flex-direction: column;
				align-items: center;
			}

			.button_yellow {
				align-self: flex-start;
				flex: 0 0 auto;
				width: auto;

				@include min540 {
					width: 100%;
					flex: 0 0 rem(54);
					margin-bottom: rem(25);
					// height: rem(54);
				}
			}

			.link {
				font-size: rem(15);
				color: var(--c-yellow);
			}
		}

		.one {

		}
	}

	.button_red, .button_green {
		padding: 0 rem(22);

		@include min1050 {
			border-color: var(--c-text-gray);
			background-color: var(--c-text-gray);
		}
	}

	&__checked {
		margin-top: rem(30);

		display: flex;
		flex-direction: column;

		.field_checked {
			margin-bottom: rem(10);
			
			&:last-of-type {
				margin-bottom: rem(14);
			}

			.check {
				border-color: var(--c-yellow);
			}
		}

		.field_textarea {
			margin-bottom: rem(28);
		}

		.field_button {
			.button_red_border {
				width: 100%;

				&:hover {
					span {
						color: white;
					}
				}

				span {
					color: var(--c-red);
				}
			}

			.button_red {
				width: 100%;
			}
		}
	}
}

.modal_order.w-600 {

	background-color: rgba(24,23,43,0.7);

	@include min768 {
		background-color: #211F3D;
	}

	.modal_order__close {
		display: block;

		@include min768 {
			display: none;
		}
	}

	.modal_order__close_smart {
		display: none;

		// @include min768 {
		// 	display: block;
		// }

		@include min768 {
			display: block;
			top: rem(35);
		}
	}

	.modal_order__header {
		// margin-top: rem(35);
	}

	.modal_order__wrap {
		width: rem(600);

		margin-left: rem(-300);

		padding: rem(44) rem(55);

		@include min540 {
			width: 100%;
			min-width: 320px;
			left: 0;
			margin-left: 0;

			padding: rem(35) rem(15);

			.field {
				height: rem(44);
			}
		}
	
	}
}

.modal_order.w-500 {

	background-color: rgba(24,23,43,0.7);

	@include min768 {
		background-color: #211F3D;
	}

	// .modal_order__close {
	// 	display: block;

	// 	@include min768 {
	// 		display: none;
	// 	}
	// }

	// .modal_order__close_smart {
	// 	display: none;

	// 	@include min768 {
	// 		display: block;
	// 		top: rem(35);
	// 	}
	// }

	.modal_order__wrap {
		width: rem(500);

		margin-left: rem(-250);

		padding: rem(58) rem(30) rem(50) rem(30);

		@include min540 {
			width: 100%;
			min-width: 320px;
			left: 0;
			margin-left: 0;

			padding: rem(35) rem(15);

			.field {
				height: rem(44);
			}
		}
	
	}
}

.modal_order.w-600.active {

	

	.modal_order__wrap {
		
		// margin-bottom: rem(100);

		@include min1600 {
			top: 65%;
		}
		

		// @include min768 {
		// 	top: %;
		// }

		// @include min

		@include min768 {
			top: 0;
			transform: translateY(0%);
		}
	}
}

.modal_order.w-500.active {
	.modal_order__wrap {

		

		@include min768 {
			top: 0;
			transform: translateY(0%);
		}
	}
}


.modal_order.active {
	animation: modal_profile_active 500ms forwards;

	.modal_order__close_smart {
		@include min768 {
			display: block;
		}
		
	}

	.modal_order__wrap {
		transition: var(--transition);
		top: 50%;
		transform: translateY(-50%);

		@include min768 {
			// top: 0;
			// transform: translateY(0%);
		}
	}
}