.field_toggle {
	cursor: pointer;
	display: flex;
	align-items: center;

	flex-direction: row-reverse;

	input {
		display: none;

		&:checked {
			~ .check {
				background-color: var(--c-yellow);

				&::before {
					transform: translateX(85%);
				}
			}
		}
	}

	.check {
		position: relative;
		border-radius: rem(30);
		height: rem(30);
		width: rem(50);
		background-color: var(--c-black);

		transition: var(--transition);

		&::before {
			content: "";
			position: absolute;
			top: rem(1.8);
			left: rem(1.8);

			transform: translateX(0);

			width: rem(25);
			height: rem(25);
			border-radius: 50%;
			background-color: white;

			transition: var(--transition);

			box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
		}
	}

	.text {
		font-size: rem(15);
		color: var(--c-text-gray-light);
		margin-right: rem(20);
	}
}

.field_set {
	position: relative;

	&__visible {
		position: absolute;
		top: rem(19);
		right: rem(18);

		svg {
			
			path {
				fill: var(--c-text-gray);
				transition: var(--transition);
			}
		}

		&:hover {
			svg {
				path {
					fill: var(--c-text-gray-light);
				}
			}
		}
	}

	&__visible.active {
		svg {
			path {
				fill: var(--c-text-gray-light);
			}
		}
	}

	&__message {
		margin-top: rem(10);

		font-size: rem(13);
		color: rgba(178,177,190,.9);
	}
}

.field {
	width: 100%;
	height: rem(54);
	border-radius: var(--ui-border-4);
	padding: 0 rem(26);

	border: rem(1.5) solid var(--c-text-gray-light);
	background-color: transparent;
	color: var(--c-text-gray-light);
	font-size: rem(15);

	transition: var(--transition);

	&:focus {
		border-color: var(--c-yellow);
	}

	@include min768 {
		font-size: rem(13);
		padding: 0 rem(20);
	}

	@include placeholder {
		color: var(--c-text-gray-light);
	}
}

.field_checked {
	cursor: pointer;

	input {
		display: none;

		&:checked {
			~ .check {
				border-color: var(--c-yellow);
				background-color: var(--c-yellow);

				svg {
					opacity: 1;
				}
			}
		}
		
	}

	display: flex;
	
	.check {
		flex: 0 0 rem(25);
		width: rem(25);
		height: rem(25);
		border-radius: rem(2);
		background-color: transparent;
		border: rem(1) solid var(--c-text-gray-light);
		margin-right: rem(24);

		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			opacity: 0;
			transition: var(--transition);
		}
	}

	.text {
		font-size: rem(13);
		line-height: 150%;
		color: var(--c-text-gray);

		span {
			color: var(--c-text-gray-light);
		}
	}
}

.field_textarea {
	textarea {
		width: 100%;
		background-color: rgba(178, 177, 190, .1);
		border-radius: rem(2);
		padding: rem(15) rem(20);
		font-size: rem(13);
		color: var(--c-text-gray);

		min-height: rem(115);
		resize: none;

		@include placeholder {
			color: var(--c-text-gray);
		}
	}

	textarea.white {
		color: white;

		@include placeholder {
			color: white;
		}
	}

	textarea.h-200 {
		min-height: rem(200);
	}
}


.calc_control {
	width: rem(265);
	height: rem(60);
	display: flex;
	align-items: center;
	justify-content: space-between;
	background-color: var(--c-text-dark);

	.plus, .minus {
		flex: 0 0 rem(60);
		width: rem(60);
		height: rem(60);
		border-radius: rem(4);
		border: rem(1) solid var(--c-yellow);

		font-size: rem(20);
		color: var(--c-yellow);

		&:hover {
			background-color: var(--c-yellow);

			color: var(--c-text-dark)
		}
	}

	.count {
		width: rem(80);
		font-size: rem(20);
		color: var(--c-text-gray-light);
		text-align: center;
		background-color: transparent;

		appearance: none;

		-moz-appearance:textfield;

		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
		}

		@include placeholder {
			color: var(--c-text-gray-light);
		}
	}
}

.field_radio {
	display: flex;
	align-items: center;
	cursor: pointer;

	input {
		display: none;

		&:checked {
			~ .check {
				&::before {
					opacity: 1;
				}
			}
		}
	}

	.check {
		flex: 0 0 rem(25);
		width: rem(25);
		height: rem(25);
		border-radius: 50%;
		border: rem(1.5) solid var(--c-yellow);

		display: flex;
		align-items: center;
		justify-content: center;
		margin-right: rem(15);

		&::before {
			content: "";

			width: rem(15);
			height: rem(15);
			border-radius: 50%;
			background-color: var(--c-yellow);

			opacity: 0;
		}
	}

	.text {
		color: var(--c-text-gray-light);
	}
}


.field_set_radio {

	display: flex;
	flex-wrap: wrap;

	> .text {
		flex: 0 0 100%;
		max-width: 100%;
		text-align: left;
		margin-bottom: rem(20);
		font-weight: 500;
		color: var(--c-text-gray);
	}

	.field_radio {
		&:first-of-type {
			margin-right: rem(74);

			@include min540 {
				margin-right: rem(25);
			}
		}
	}
}

.field_set_card {
	display: flex;
	flex-wrap: wrap;

	> .field {
		// margin-bottom: rem(12);
	}

	.field_cvv {
		flex: 0 0 rem(127);
		max-width: rem(127);
		margin-right: rem(20);
		position: relative;

		margin-top: rem(12);

		@include min540 {
			flex: 1;
			max-width: 100%;
			margin-right: rem(10);
		}

		.required {
			position: absolute;
			top: rem(9);
			right: rem(7);
			width: rem(10);
			height: rem(10);
			border-radius: 50%;
			background-color: var(--c-yellow);

			display: flex;
			align-items: center;
			justify-content: center;

			font-weight: 500;
			font-size: rem(10);
			color: #222134;
		}

		.field {
			text-align: center;
			
			appearance: none;

			-moz-appearance:textfield;

			&::-webkit-outer-spin-button,
			&::-webkit-inner-spin-button {
				-webkit-appearance: none;
			}
		}
	}

	.field_data {
		flex: 0 0 rem(177);
		max-width: rem(177);
		margin-top: rem(12);

		@include min540 {
			flex: 1;
			max-width: 100%;
		}
	}
}

.field_set_code {
	position: relative;
	display: flex;

	.field {
		position: relative;
		flex: 0 0 100%;
		max-width: 100%;
		padding-right: rem(210);

		@include min540 {
			padding-right: rem(146);
		}
	}

	.button_yellow_border {
		position: absolute;
		top: 0;
		right: 0;

		border-top-left-radius: 0;
		border-bottom-left-radius: 0;

		@include min540 {
			flex: 0 0 rem(132);
			max-width: rem(132);
			height: rem(44);
			padding: 0 rem(15);

			span {
				font-size: rem(13);
			}
		}
	}
}