.custom_select, .custom_select_search {
	
	.ss-single-selected {
		height: rem(46) !important;
		background-color: #18172A !important;
		border-radius: rem(2) !important;
		border: rem(1) solid #8D8BA7 !important;
		padding: 0 rem(25) !important;

		transition: var(--transition) !important;

		.placeholder {
			color: var(--c-text-gray-light) !important;
			font-size: rem(15) !important;
		}

		.ss-arrow {
			margin: 0 0 0 rem(20) !important;

			@include min540 {
				margin: 0 0 0 rem(10) !important;
			}

			.arrow-down {
				border-color: var(--c-text-gray-light) !important;
			}
		}
	}

	

	.ss-open-below {
		border-bottom: 0 !important;

		~ .ss-content {
			border-top: 0 !important;

			.ss-list {
				border-top: 0 !important;

				.ss-option:first-child {
					padding: 16px 10px 7px 10px;
					border-top: rem(1) solid #8D8BA7;
				}
			}
		}
	}

	.ss-open-above {
		border-top: 0 !important;

		~ .ss-content {
			border-bottom: 0 !important;

			.ss-list {
				border-bottom: 0 !important;

				.ss-option:last-child {
					padding: 7px 10px 16px 10px;
					border-bottom: rem(1) solid #8D8BA7;
				}
			}
		}

		~ .ss-content.ss-open {
			.ss-search {
				order: 2;
			}
		}
	}

	.ss-content {
		// background-color: var(--c-black) !important;
		// border: 0 !important;

		// .ss-list {
		// 	.ss-option {
		// 		// padding: rem(12) rem(20) !important;
		// 		// height: rem(46) !important;
		// 		// font-size: rem(15) !important;
		// 		color: var(--c-text-gray-light) !important;

		// 		display: flex;
		// 		align-items: center;

		// 		&:hover {
		// 			color: var(--c-black) !important;
		// 			background-color: var(--c-yellow) !important;
		// 		}
		// 	}

		// 	.ss-option-selected {
		// 		background-color: var(--c-yellow) !important;
		// 		color: var(--c-black) !important;
		// 	}
		// }

		.ss-search {
			background-color: #18172A !important;
			border-left: rem(1) solid #8D8BA7;
			border-right: rem(1) solid #8D8BA7;

			padding: 8px 15px 6px !important;

			input {
				border-radius: rem(2);
				background-color: rgba(178,177,190,0.1);
				box-shadow: none !important;
				border: 0 !important;
				height: rem(46) !important;
				color: var(--c-text-gray) !important;
				font-size: rem(13) !important;
			}
		}
	}

	.ss-content.ss-open {
		display: flex !important;
		flex-direction: column !important;
	}

}

.custom_select_search {
	.ss-list {
		max-height: rem(240) !important;
	}
}