.abs_message_top {
	width: 1280px;

	position: fixed;
	z-index: 50;
	top: rem(16);
	left: 50%;
	margin-left: rem(-640);

	display: flex;
	align-items: center;
	justify-content: center;

	height: rem(50);
	background-color: #2B2A3D;
	border-radius: rem(10);
	animation: messageShow 500ms forwards;

	// border: rem(2) solid #27AE60;

	@include min1280 {
		width: calc(100% - 30px);
		// margin-left: 15px;
		// margin-right: 15px;
		left: 15px;
		margin-left: 0;
	}

	.text {
		font-size: rem(15);
		color: var(--c-text-gray-light);

		@include min540 {
			font-size: rem(12);
		}
	}

	.close {
		position: absolute;
		top: rem(10);
		right: rem(10);
	}
}

.abs_message_top.green {
	border: rem(2) solid #27AE60;
}

.abs_message_top.yellow {
	border: rem(2) solid var(--c-yellow);
}

.abs_message_top.red {
	border: rem(2) solid var(--c-red);
}

.abs_message_top.messageRemove {
	animation: messageRemove 500ms forwards;
}

@keyframes messageShow {
	0% {
		opacity: 0;
		top: rem(-50);
	}

	100% {
		opacity: 1;
		top: rem(16);
	}
}

@keyframes messageRemove {
	0% {
		opacity: 1;
		top: rem(16);
	}

	100% {
		opacity: 0;
		top: rem(-50);
	}
}