.page_puzzle {
	margin-bottom: rem(50);
	
	.title {
		margin-bottom: rem(40);
		color: var(--c-text-gray-light);
		font-size: rem(25);

		@include min1050 {
			margin-bottom: rem(20);
		}

		@include min768 {
			margin-bottom: rem(15);
			font-size: rem(20);
		}
	}

	.text {
		line-height: 140%;
		color: var(--c-text-gray);
		margin-bottom: rem(100);
		max-width: rem(630);

		@include min1050 {
			margin-bottom: rem(80);
		}

		@include min768 {
			margin-bottom: rem(50);
		}
	}

	.items {
		display: flex;
		flex-wrap: wrap;

		.item {
			flex: 0 0 calc((100% - 40px) / 3);
			max-width: calc((100% - 40px) / 3);
			margin-right: rem(20);
			margin-bottom: rem(20);

			&:nth-child(3n) {
				

				@include max1280 {
					margin-right: 0;
				}
			}

			background-color: #242336;
			border-radius: rem(50);
			height: rem(100);
			padding: rem(5);

			display: flex;
			flex-wrap: wrap;
			align-items: center;

			@include min1280 {
				flex: 0 0 calc((100% - 20px) / 2);
				max-width: calc((100% - 20px) / 2);

				&:nth-child(2n) {
					margin-right: 0;
				}
			}

			@include min768 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-right: 0;

				background-color: transparent;
				height: auto;
				padding: 0;
				border-radius: 0;
				margin-bottom: rem(15);

				// align-items: flex-start;

				&:last-child {
					margin-bottom: 0;
				}
			}

			&__image {
				background-color: var(--c-text-gray);
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 50%;
				margin-right: rem(15);

				flex: 0 0 rem(90);
				max-width: rem(90);
				height: rem(90);

				@include min768 {
					flex: 0 0 rem(40);
					max-width: rem(40);
					height: rem(40);
					margin-right: rem(10);
					
					svg {
						max-width: 52%;
					}
				}
			}

			&__text {
				flex: 1;
				display: flex;
				align-items: center;
				p {
					font-size: rem(15);
					font-weight: 500;
					line-height: 140%;
					color: var(--c-text-gray);

					@include min540 {
						font-size: rem(14);
					}
				}
			}
		}

		.item.active {
			.item__image {
				background-color: var(--c-yellow);
			}
		}
	}

}

.puzzle__slider {
	max-width: rem(890);
	margin-bottom: rem(50);

	margin-left: auto;
	margin-right: auto;

	@include min540 {
		margin-bottom: rem(30);
	}

	.swiper {
		margin-bottom: rem(60);
		height: rem(677);

		@include min768 {
			width: rem(290);
			height: rem(218);
			margin-bottom: rem(30);
		}

		.swiper-slide {
			height: rem(677);
			

			@include min768 {
				height: rem(218);
			}
		}
	}

	.puzzle_item {
		height: 100%;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			vertical-align: middle;
		}
	}

	&_nav {
		display: flex;
		justify-content: center;

		.swiper_control {
			@include min540 {
				width: 100%;
				justify-content: space-between;
			}
		}

		.swiper_control__left, .swiper_control__right {
			@include min540 {
				display: block;
			}
		}

		.swiper_control__bullet {

			@include min540 {
				margin: 0;
				width: auto !important;
			}
			
		}
	}
}