.page_rules {
	.title_h2 {
		color: var(--c-text-gray-light);
		margin-bottom: rem(30);
	}

	> p {
		max-width: rem(738);
	}

	p {
		color: var(--c-text-gray);
		line-height: 140%;

		@include min540 {
			font-size: rem(15);
		}
	}


	&__wrap {
		margin-top: rem(88);
		margin-bottom: rem(100);
		background-color: rgba(36, 35, 54, .8);
		border-radius: rem(5);
		padding: rem(51) rem(65);

		@include min540 {
			padding: 0;
			background-color: transparent;
			border-radius: 0;

		}

		.box {
			max-width: rem(758);
		}

		.subtitle {
			font-size: rem(20);
			margin-bottom: rem(36);
			color: var(--c-text-gray-light);

			@include min540 {
				margin-bottom: rem(25);
			}
		}


		.white {
			color: var(--c-text-gray-light);
		}

		.mb-55 {
			margin-bottom: rem(55);
		}

		b {
			display: block;
			font-weight: 500;

			@include min540 {
					font-size: rem(15);
				}
		}

		ol {
			margin-top: rem(40);
			margin-bottom: rem(55);

			li {
				margin-bottom: rem(20);
				line-height: 140%;
				color: var(--c-text-gray);
				list-style: decimal;
				margin-left: rem(20);

				@include min540 {
					font-size: rem(15);
				}

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		ul {
			margin-bottom: rem(90);

			li {
				display: flex;

				@include min540 {
					font-size: rem(15);
				}

				svg {
					flex: 0 0 rem(13);
					margin-top: rem(4);
					margin-right: rem(8);
				}

				margin-bottom: rem(20);
				color: var(--c-text-gray);
				line-height: 140%;
			}
		}
	}
}