.page_message {
	// margin-left: rem(280);
	// margin-right: rem(280);

	display: flex;
	flex-direction: column;
	align-items: center;

	margin-bottom: rem(120);

	.title_h2 {
		color: white;
		margin-bottom: rem(30);

		margin-left: auto;
		margin-right: auto;

		br {
			display: none;

			@include min540 {
				display: block;
			}
		}

		@include min540 {
			margin-bottom: rem(20);
			text-align: center;
		}
	}

	.text {
		margin-bottom: rem(58);

		font-size: rem(20);
		text-align: center;
		line-height: 140%;
		color: var(--c-text-gray-light);

		max-width: rem(710);

		@include min540 {
			max-width: 100%;
			margin-bottom: rem(40);
			font-size: rem(15);
		}

		a {
			font-weight: 500;
		}
	}

	.form {
		max-width: rem(472);
		width: 100%;
		margin-left: auto;
		margin-right: auto;

		display: flex;
		flex-direction: column;

		.field {
			margin-bottom: rem(20);
			height: rem(46);
			border: 0;
			background-color: rgba(178,177,190,.1);
			border-radius: rem(2);
			color: var(--c-text-gray);
			padding: 0 rem(25);
		}

		textarea.field {
			padding: rem(14) rem(25);
			min-height: rem(132);
			resize: none;
			margin-bottom: rem(32);
		}
	}
}