.lk_sect_filter {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(100);

	> .flex {
		margin-bottom: rem(30);
		justify-content: space-between;
		align-items: center;

		button {
			display: none;
		}

		@include min540 {
			button {
				display: block;
			}
		}
	}

	.title {
		// margin-bottom: rem(30);
		font-size: rem(25);
		color: white;

		@include min {
			font-size: rem(20);
			// margin-bottom: rem(47);
		}
	}

	&__buttons {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(25);

		@include min540 {
			display: none;
		}

		.item {
			border-radius: rem(2);
			border: rem(1.5) solid #2D2B49;
			height: rem(54);
			padding: 0 rem(40);
			display: flex;
			align-items: center;
			font-weight: 500;
			color: var(--c-text-gray);

			margin-right: rem(10);

			@include min {
				flex: 0 0 calc((100% - 10px) / 2);
				max-width: calc((100% - 10px) / 2);
				justify-content: center;
				margin-bottom: rem(10);

				&:nth-child(3), &:last-child {
					margin-bottom: 0;
				}

				&:nth-child(2n) {
					margin-right: 0;
				}
			}

			@include min540 {
				height: rem(40);
				padding: 0 rem(10);
				font-weight: normal;
				font-size: rem(13);
			}

			&:hover {
				background-color: #2D2B49;
				color: white;
			}
		}

		.item.active {
			background-color: #2D2B49;
			color: white;
		}

		.item_last {
			margin-left: auto;

			display: flex;
			align-items: center;

			@include min {
				margin-left: 0;
				// margin-right: rem(10);
				flex: 0 0 calc((100% - 10px) / 2);
				max-width: calc((100% - 10px) / 2);

				justify-content: center;
			}

			&:hover {

				svg {
					path {
						fill: var(--c-yellow) !important;
					}
				}

				span {
					color: var(--c-yellow) !important;
				}
			}

			span {
				margin-left: rem(10);
				font-size: rem(15);
				color: var(--c-text-gray);

				@include min540 {
					font-size: rem(13);
				}
			}
		}
	}

	&__buttons.active {

		@include min540 {
			display: flex;
		}
	}

	&__text {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		@include min {
			flex-direction: column;
			align-items: flex-start;
		}

		.item {
			display: flex;
			align-items: center;
			margin-right: rem(40);

			@include min {
				margin-right: 0;
				margin-bottom: rem(15);
				

			}

			&:last-child {
				margin-right: 0;

				@include min {
					margin-bottom: 0;
				}
			}

			.icon {
				margin-right: rem(15);
				width: rem(15);
				height: rem(15);

				@include min540 {
					margin-right: rem(10);
				}
			}

			span {
				font-size: rem(15);
				color: white;

				@include min540 {
					font-size: rem(13);
				}
			}
		}

		.item.item_blue {
			.icon {
				background-color: var(--c-blue-accent);
			}
			
		}

		.item.item_green {
			.icon {
				background-color: #6FCF97;
			}
			
		}

		.item.item_gray {
			.icon {
				background-color: var(--c-text-gray);
			}
			
		}
	}
}

.lk_sect_filter.mb-m {
	margin-bottom: rem(12);
}

.table_order_col {

		
	

	.table_order__header {
		// @include min991 {
		// 	justify-content: space-between;
		// }
	}

	.table_order__header>span:nth-child(2) {
		span {
			display: none;

			@include min540 {
				display: block;
			}
		}
	}

	.table_order__header>span:nth-child(4) {
		color: var(--c-text-gray);
		@include min991 {
			display: flex;
			flex: 0 0 auto;
			justify-content: flex-end;
		}
	}

	.table_order__header>span:nth-child(5) {
		@include min991 {
			display: none;
		}
	}

	.table_order__rows {
		margin-bottom: rem(40);

		@include min991 {
			margin-bottom: rem(10);
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	.table_order__row {

		@include min991 {
			padding-bottom: 0;
		}

		.row {
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			.text-underline {
				text-decoration: none;
				border-bottom: rem(1.5) solid var(--c-text-gray-light);

				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;

				@include min540 {
					text-overflow: unset;
					white-space: normal;
					border-bottom: 0;
				}
			}



			.tb_item {

				&:first-child {
					@include min991 {
						justify-content: flex-start;
						// margin-left: rem(24);
						margin-top: rem(3);
					}
					
				}

				&:nth-child(2) {
					@include min540 {
						flex: 0 0 65%;
						max-width: 65%;
					}
				}

				&:nth-child(3) {
					@include min991 {
						margin-right: 0;
					}

					@include min540 {
						padding: rem(12) 0;

						.title {
							color: var(--c-text-gray);
						}
					}
				}

				&:nth-child(4) {
					

					@include min991 {
						order: 9;
						flex: 0 0 10%;
						max-width: 10%;
						border-top: 0;
						padding: 0;
					}

					@include min540 {
						align-items: flex-start;
						padding: rem(12) 0;

						.title {
							color: var(--c-text-gray);
						}
					}
				}
				&:last-child {
					@include min991 {
						order: 10;
						flex: 0 0 100%;
						max-width: 100%;
						border-top: rem(1.5) solid #18172A;
						padding-top: rem(5);
						justify-content: flex-start;
					}

					@include min540 {
						padding-left: 0;

						.user_card {
							align-items: flex-start;
							// flex: 0 0 65%;
							// max-width: 65%;
						}

						.title {
							white-space: normal;
							font-weight: normal;
						}
					}
				}
			}
		}

		.row:last-child {
			margin-top: rem(5);

			@include min991 {
				background-color: #18172A;
				// margin-top: rem(8);
				// border-top: rem(10) solid #18172A;
			}

			@include min540 {
				
			}

			.toggle_wrap {
				width: 100%;
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				
				@include min991 {
					display: none;
				}
			}

			.toggle_wrap.active {
				display: flex;
			}

			.tb_item {
				position: relative;
				flex: 1;
				height: rem(30);
				// margin-right: rem(10);

				display: flex;
				justify-content: center;

				.icon {
					position: absolute;
					top: rem(5);
					right: rem(6);
					width: rem(10);
					height: rem(10);
					background-color: var(--c-yellow);
					border-radius: 50%;

					display: flex;
					align-items: center;
					justify-content: center;

					font-weight: 500;
					font-size: rem(10);
					color: #222134;
				}

				@include min991 {
					height: rem(40);
					font-size: rem(15);
					background-color: rgba(36, 35, 54, .8);
					margin-top: rem(10);
				}

				@include min540 {
					background-color: rgba(141,139,167,0.1);
					position: relative;

					&::before {
						content: "";
						position: absolute;
						top: rem(-10);
						left: -100%;
						width: 9999em;
						height: rem(10);
						background-color: var(--c-bg);
					}
				}

				&:hover {
					
					
					@include max540 {
						background-color: #2D2B49;
					}
				}

				&:first-child {
					flex: 0 0 32.5%;
					max-width: 32.5%;

					@include min991 {
						flex: 0 0 100%;
						max-width: 100%;
						// border-bottom: rem(10) solid #18172A;
						margin-left: 0;
					}
				}

				&:nth-child(2) {
					flex: 0 0 33.2%;
					max-width: 33.2%;

					@include min991 {
						flex: 0 0 100%;
						max-width: 100%;
						// border-bottom: rem(10) solid #18172A;
						// margin-left: 0;
					}
				}

				&:nth-child(3) {
					flex: 0 0 30%;
					max-width: 30%;

					@include min991 {
						flex: 0 0 100%;
						max-width: 100%;
						// border-bottom: rem(10) solid #18172A;
						// margin-left: 0;
					}
					@include min540 {
						align-items: center;
					}
				}

				color: var(--c-text-gray);
				font-weight: 500;
			}

			.tb_item.flex-1 {
				margin-right: rem(10);

				@include min991 {
					margin-right: 0;
				}
			}

			.tb_item.text-block {
				margin-right: 0;
				
				font-size: rem(15);
				font-weight: normal;
				color: var(--c-text-gray);

				&:hover {
					background-color: rgba(36,35,54,0.8);
				}

				@include min991 {
					// height: auto;
					text-align: center;
					// padding: rem(6) rem(20) rem(24) rem(20);
					// margin-top: 0;
					// border-top: 0;

					&::before {
						// display: none;
					}
				}

				@include min540 {
					font-size: rem(13);
				}
			}

			.tb_item.green {
				color: #6FCF97;
			}

			.tb_item.red {
				color: #EB5757;
			}

			.tb_item.last {
				flex: 0 0 rem(30);
				width: rem(30);
				display: flex;
				align-items: center;
				justify-content: center;
				background-color: #242336;
				margin-right: 0;

				@include min991 {
					flex: 0 0 100%;
					max-width: 100%;
					padding: 0;
					margin: 0;
					margin-top: rem(10);
					border-bottom: 0;
					// margin-left: 0;
				}

				&:hover {
					background-color: #2D2B49 !important;
				}
			}

			.button_toggle {
				order: 9999;
				height: rem(40);
				width: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
				border-top: rem(2) solid #222134;
				border-bottom: rem(2) solid #222134;
				position: relative;
				margin-top: rem(10);

				@include max991 {
					display: none;
				}

				svg {
					position: relative;
					z-index: 2;
				}

				&::before {
					content: "";
					position: absolute;
					top: 0;
					left: -100%;
					width: 999em;
					height: 100%;
					background-color: #18172A;
				}

				&::after {
					content: "";
					position: absolute;
					top: rem(-11);
					left: -100%;
					width: 9999em;
					height: rem(9);
					background-color: #18172A;
				}
			}
		}
	}

	.table_order__rows.active {
		.toggle_wrap {
			display: flex !important;
		}

		.button_toggle {
			display: none !important;
		}
	}

	.row-left-blue {
		position: relative;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: rem(15);
			height: 100%;
			background-color: var(--c-blue-accent);
			z-index: 1;

			@include min991 {
				top: rem(12);
				left: rem(15);
				height: rem(15);
			}
		}

		.row:first-child {
			.tb_item {
				&:first-child {
					@include min991 {
						margin-left: rem(24);
					}
				}
			}
		}

		
	}

	.row-left-green {
		position: relative;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: rem(15);
			height: 100%;
			background-color: #6FCF97;
			z-index: 1;

			@include min991 {
				top: rem(12);
				left: rem(15);
				height: rem(15);
			}
		}

		.row:first-child {
			.tb_item {
				&:first-child {
					@include min991 {
						margin-left: rem(24);
					}
				}
			}
		}
	}

	.row-left-gray {
		position: relative;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: rem(15);
			height: 100%;
			background-color: var(--c-text-gray);
			z-index: 1;

			@include min991 {
				top: rem(12);
				left: rem(15);
				height: rem(15);
			}
		}

		.row:first-child {
			.tb_item {
				&:first-child {
					@include min991 {
						margin-left: rem(24);
					}
				}
			}
		}
	}

	.row-left-graylight {
		position: relative;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: rem(15);
			height: 100%;
			background-color: var(--c-text-gray-light);
			z-index: 1;

			@include min991 {
				top: rem(12);
				left: rem(15);
				height: rem(15);
			}
		}

		.row:first-child {
			.tb_item {
				&:first-child {
					@include min991 {
						margin-left: rem(24);
					}
				}
			}
		}
	}

	.item-disabled {
		.row:first-child {
			position: relative;

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: #242336;
				opacity: 0.7;
			}
		}
	}

}

.table_adds {
	.table_order__header {
		.w-50 {
			margin-right: auto;
		}
	}

	.table_order__row {
		.row:first-child {
			.tb_item {
				margin-right: rem(10);

				&:last-child {
					margin-right: 0;
				}

				@include min991 {

					&:nth-child(2) {
						flex: 0 0 60% !important;
						max-width: 60% !important;
					}

					&:nth-child(3) {
						flex: 0 0 rem(47);
						max-width: rem(47);
						justify-content: flex-end;
						padding: 0 rem(10);
						// padding-top: rem(10);
					}

					&:last-child {
						margin-right: 0;
						flex: 0 0 rem(47);
						max-width: rem(47);
						padding-top: rem(10);
						border-top: 0;
						padding: 0 rem(10);
						justify-content: flex-end;
					}
				}

				@include min540 {
					&:nth-child(3) {
						padding-top: rem(10);
					}

					&:last-child {
						padding-top: rem(10);
					}
				}
				
			}
		}
	}
}