.sect_lk_header {
	margin-bottom: rem(30);

	.title {
		font-size: rem(25);
		color: white;

		@include min540 {
			font-size: rem(20);
		}
	}

	@include min768 {
		.flex {
			flex-direction: column;
			align-items: flex-start;

			.title {
				flex: 0 0 100%;
				align-self: flex-start;
				margin-bottom: rem(55);
			}

			.button_black {
				width: 100%;
			}
		}
	}

	.button_black {
		width: rem(305);
	}
}

.sect_lk_form {
	display: flex;
	justify-content: center;
	margin-bottom: rem(50);

	@include min540 {
		// margin-left: -15px;
		// margin-right: -15px;
		width: 100%;
	}
}

.form_wrap {
	background-color: rgba(36, 35, 54, .8);
	border-radius: rem(5);
	padding: rem(60) rem(50);

	width: rem(500);

	@include min540 {
		position: relative;
		width: 100%;
		padding: rem(60) 0;

		display: flex;
		flex-direction: column;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: -100%;
			width: 9999em;
			height: 100%;
			background-color: rgba(36, 35, 54, .8);
			z-index: -1;
		}
	}

	.field {
		height: rem(46);
		background-color: rgba(178, 177, 190, .1);
		border-radius: rem(2);
		border: 0;

		color: var(--c-text-gray);
		padding: 0 rem(25);

		@include placeholder {
			color: var(--c-text-gray);
		}

		@include min540 {
			font-size: rem(13);
		}
	}

	textarea.field {
		height: rem(132);
		padding: rem(14) rem(25);
		resize: none;

		@include min540 {
			padding: rem(14) rem(20);
		}
	}

	&__sect {
		margin-bottom: rem(20);

		display: flex;
		justify-content: space-between;

		@include min540 {
			flex-wrap: wrap;
		}

		.custom_select, .custom_select_search {
			flex: 1;
			margin-right: rem(20);

			@include min540 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-right: 0;
				margin-bottom: rem(15);

				.ss-single-selected {
					justify-content: flex-start !important;

					.placeholder {
						justify-content: flex-start !important;
					}
				}
			}

			&:last-child {
				margin-right: 0;

				@include min540 {
					margin-bottom: 0;
				}
			}
		}
	}

	&__field {
		margin-bottom: rem(20);

		span {
			flex: 0 0 100%;
			font-size: rem(13);
			font-weight: 500;
			color: var(--c-text-gray);
			margin-top: rem(2);
			margin-left: rem(10);
		}
	}

	&__field_group {
		margin-top: rem(40);

		display: flex;

		@include min540 {
			margin-top: rem(30);
		}

		.field {
			flex: 1;
			margin-right: rem(20);

			&:last-child {
				margin-right: 0;
			}
		}

		.field-set {
			flex: 1;
			margin-right: rem(20);
			margin-bottom: rem(40);

			display: flex;
			flex-direction: column;

			&:last-child {
					margin-right: 0;
				}

			.field {
				margin-right: 0;
				flex: 0 0 rem(46);
			}

			span {
				flex: 0 0 100%;
				font-size: rem(13);
				font-weight: 500;
				color: var(--c-text-gray);
				margin-top: rem(2);
				margin-left: rem(10);
			}
		}

		.field-set-icon {
			position: relative;

			svg {
				position: absolute;
				top: rem(13);
				right: rem(20);
			}
		}
	}

	&__field_group.order-1 {
		margin-bottom: rem(40);

		@include min540 {
			order: 10;
			margin-bottom: 0;
			margin-top: 0;
		}

		.field {
			flex: 0 0 calc(50% - 10px);
			max-width: calc(50% - 10px);
		}
	}

	&__button {
		margin-top: rem(50);

		@include min540 {
			margin-top: rem(40);

			order: 11;
		}

		button {
			width: 100%;
		}

		.flex {
			align-items: center;

			@include min540 {
				flex-direction: column;
			}

			.button_yellow {
				margin-right: rem(26);

				@include min540 {
					margin-right: 0;
				}
			}

			.link {
				font-size: rem(15);
				font-weight: 500;
				color: var(--c-text-gray);

				@include min540 {
					margin-top: rem(38);
				}
			}
		}
	}
}