.sect_border {
	padding-top: rem(64);
	padding-bottom: rem(60);
	border-bottom: rem(1.5) solid rgba(224, 222, 244, .3);

	@include min540 {
		padding-bottom: rem(42);
		padding-top: rem(46);
	}

	&:first-child {
		padding-top: 0;
	}

	&:last-child {
		border-bottom: 0;
	}

	.title {
		font-size: rem(25);
		color: var(--c-text-gray-light);
	}

	.subtitle {
		font-size: rem(20);
		margin-bottom: rem(35);
		color: var(--c-text-gray-light);
	}

	.item {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		&__left {
			margin-right: rem(66);

			flex: 0 0 rem(150);

			@include min {
				flex: 0 0 100%;
				display: flex;
				justify-content: center;
				margin-bottom: rem(40);
			}

			.avatar {
				width: 100%;
				height: 100%;
				border-radius: 50%;
				overflow: hidden;

				@include min {
					width: rem(100);
					height: rem(100);
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			.icon {
				width: rem(150);
				height: rem(150);
				border-radius: 50%;
				background-color: var(--c-yellow);

				display: flex;
				align-items: center;
				justify-content: center;

				@include min {
					width: rem(100);
					height: rem(100);
					
					svg {
						width: rem(33);
					}
				}
			}
		}

		&__right {
			display: flex;
			flex-wrap: wrap;
			flex: 1;

			@include min {
				justify-content: center;
			}

			.flex-col {

				@include min540 {
					width: 100%;
				}

				.flex {
					margin-bottom: rem(15);

					@include min540 {
						flex-direction: column;
						

						.button_black {
							margin-bottom: rem(15);
							width: 100%;

							&:last-child {
								margin-bottom: 0;
							}
						}
					}

					&:last-child {
						margin-bottom: 0;
					}
				}
			}

			.w-250 {
				width: rem(252);

				@include min540 {
					width: 100%;
				}
			}

			.button_black {
				margin-right: rem(20);

				&:last-child {
					margin-right: 0;
				}
			}

			.text {
				display: flex;
				flex-direction: column;

				@include min {
					align-items: center;
				}

				@include min540 {
					align-items: flex-start;
				}

				&_title {
					margin-bottom: rem(15);
					font-weight: 500;
					color: var(--c-text-gray-light);
				}

				&_content {
					margin-bottom: rem(15);
					font-size: rem(15);
					color: var(--c-text-gray);

					@include min {
						text-align: center;
					}

					@include min540 {
						text-align: left;
					}
				}

				.button_black {
					align-self: flex-start;

					@include min {
						align-self: center;
					}

					@include min540 {
						width: 100%;
					}
				}
			}
		}
	}

}